import React from "react";
import TrackGiftsSidebar from "./containers/track-gifts-sidebar/TrackGiftsSidebar";
import TrackGiftsContent from "./containers/track-gifts-content/TrackGiftsContent";
import Search from "./components/search/Search";
import AddGift from "./components/add-gift/AddGift";
import Button from "../commons/components/button-component/ButtonComponent";
import Modal from "../commons/components/modal-component/ModalComponent";
import "./TrackGifts.scss";
import ThankYouList from "../track-gifts/components/thank-you-list/ThankYouList";

class TrackGifts extends React.Component {

  state = {
    showTrackFilterModal: false,
    showSearchPopUp: false
  }

  searchRef = React.createRef();

  closeTrackFilterModal = () => {
    this.setState({showTrackFilterModal: false})
  }

  toggleSearchBar = e => {
    e.preventDefault()
    if (this.searchRef.current) {
      this.searchRef.current.classList.toggle("track-gifts-show-search-dialog")
    }
  }

  trackFilterModalFooterButtons =
    (
      <>
        <Button
          variant="secondary"
          className="px-3 py-1 font-weight-bold font-medium text-body"
          disabled={false}
          onClick={this.closeTrackFilterModal}
        >Close</Button>
      </>
    )

  render() {
    return (
      <div className="container track-gifts-container mt-2 mt-md-8">
        <div className="row">

          {/* Sidebar content */}
          <div className="col-2 d-none d-lg-inline-block">
            <div className="row mb-5">
              <div className="col">
                <h4 className="font-medium">View</h4>
              </div>
            </div>
            <div className="row">
              <div className="col">
                <TrackGiftsSidebar />
              </div>
            </div>
          </div>

          {/* Main Content */}
          <div className="col-12 col-lg-10">
            <div className="row mb-0 mb-md-5 align-items-center">
              <div className="col-2 col-md">
                <h4 className="font-medium">Gifts</h4>
              </div>
              <div className="col-10 justify-content-end d-lg-none d-block d-flex align-items-center mb-2">
                <Button className="d-flex py-1 mr-2" variant="secondary" onClick={(e) => this.toggleSearchBar(e)}>
                  <i className="icon-search search-icon-mobile" />
                </Button>
                <div ref={this.searchRef} className="track-gifts-search-box-container">
                  <Search componentRef={this.searchRef} />
                </div>
                <Button className="d-flex py-1 mr-1" variant="secondary" onClick={() => {this.setState({showTrackFilterModal: true})}}>
                  <i className="icon-filter-1 filter-icon" />
                </Button>
                <AddGift />
              </div>
              <div className="col-lg-7 col-xl-6 d-none d-lg-flex">
                <div className="flex-fill mr-3">
                  <Search />
                </div>
                <AddGift />
              </div>
            </div>

            <div className="row">
              <div className="divider-div d-inline-block d-md-none my-2" />
              <div className="col">
                <TrackGiftsContent />
              </div>
            </div>
            <div className="d-inline-block d-lg-none mb-11 mt-3">
              <ThankYouList />
            </div>
          </div>
        </div>
        <Modal
          show={this.state.showTrackFilterModal}
          title={"Filters"}
          closeModal={this.closeTrackFilterModal}
        >
          <TrackGiftsSidebar />
        </Modal>
      </div>
    );
  }
}

export default TrackGifts;